/**
 * @component Touchable
 * @version 3.0.0
 * @description `Touchable` "" `dom` 
 *  `dom`  `tap` ""/ `tap`  `Touchable`
 *  `tap`  `dom`
 *
 * @author jiao.shen
 * @instructions {instruInfo: ./touchable.md}{instruUrl: touchable.html?hideIcon}
 */
import React, { Component, PropTypes } from 'react';
import gesture from './gesture';

export default class Touchable extends Component {

    static propTypes = {
        /**
         * @property touchClass
         * @type String
         * @default null
         * @description TouchableclassNameNative()
         */
        touchClass: PropTypes.string,
        /**
         * @property onTap
         * @type Function
         * @default null
         * @param {DOMElement} target taptarget
         * @description TouchableonTap
         */
        onTap: PropTypes.func,
        /**
         * @skip List
         */
        onTouchStart: PropTypes.func,
        /**
         * @skip 
         */
        internalUse: PropTypes.bool,
        children: PropTypes.object
    };

    static defaultProps = {
        onTouchStart: () => {
        },
        touchClass: null,
        onTap: () => {
        },
        internalUse: false
    };

    static contextTypes = {
        scroller: PropTypes.object,
        swipeMenuList: PropTypes.object
    };

    render() {
        if (process.env.NODE_ENV !== 'production') {
            if (this.props.touchClass == null && !this.props.internalUse) {
                console.error('yo-touchable: TouchabletouchClass, , ');
            }
        }

        const onlyChild = React.Children.only(this.props.children);
        const gestureObj = gesture(
            this,
            this.context.scroller,
            this.context.swipeMenuList,
            this.props.touchClass,
            this.props.onTap,
            this.props.onTouchStart
        );
        const { onTouchStart, onTouchMove, onTouchEnd, onTouchCancel } = gestureObj;

        return React.cloneElement(onlyChild, { onTouchStart, onTouchMove, onTouchEnd, onTouchCancel });
    }
}



