/**
 * @component Sticky
 * @description `Sticky`  `Scroller`  `staticSection` 
 *  `Scroller` 
 *
 * `Sticky`  `dom` 
 * @instructions {instruInfo: ./sticky.md}{instruUrl: scroller/sticky.html?hideIcon}
 * @author jiao.shen
 * @version  3.0.2
 */
import React, { Component, PropTypes } from 'react';
import ReactDOM from 'react-dom';
import { getElementOffsetY } from '../common/util';

export default class Sticky extends Component {

    static propTypes = {
        /**
         * @property stickyExtraClass
         * @type String
         * @default null
         * @description StickyScrollersticky
         */
        stickyExtraClass: PropTypes.string,
        /**
         * @property height
         * @type number
         * @default null
         * @version 3.0.6
         * @description infinitestaticSection
         */
        height: PropTypes.number,
        children: PropTypes.oneOfType([PropTypes.array, PropTypes.object, PropTypes.string])
    };

    static defaultProps = {
        stickyExtraClass: ''
    };

    static contextTypes = {
        scroller: PropTypes.object
    };

    constructor() {
        super();
        this.domNode = null;
        this.height = null;
        this.offsetTop = null;
        this.className = null;
    }

    componentDidMount() {
        this.scroller = this.context.scroller;

        if (this.scroller) {
            this.initialize();
            this.scroller.stickyHeaders.push(this);
        }
    }

    componentDidUpdate() {
        this.initialize();
    }

    componentWillUnmount() {
        if (this.scroller) {
            this.scroller.stickyHeaders = this.scroller.stickyHeaders.filter((header) => header !== this);
        }
    }

    initialize() {
        const { height } = this.props;
        this.domNode = ReactDOM.findDOMNode(this);
        if (height == null) {
            this.height = this.domNode.offsetHeight;
        } else {
            this.height = this.props.height;
        }
        this.offsetTop = getElementOffsetY(this.domNode, null);
        this.className = this.domNode.className;
        this.onlyChild = React.Children.only(this.props.children);
        this.stickyExtraClass = this.props.stickyExtraClass;
    }

    render() {
        return React.Children.only(this.props.children);
    }
}


