import ListModel from './ListCore';
import React, { Component, PropTypes } from 'react';
import Scroller from '../../scroller/src';
import ListItem from './ListItem';
import LazyImage from '../../lazyimage';
import classNames from 'classnames';
import {
    getArrayByLength,
    DELAY_TIME_FOR_INFINITE_WITHOUT_HEIGHT,
    inheritProps
} from '../../common/util';
import '../../common/tapEventPluginInit';
import './style.scss';

const defaultProps = {
    infinite: false,
    offsetY: 0,
    infiniteSize: 12,
    itemTouchClass: 'item-touch',
    onScroll() {
    },
    onInfiniteAppend() {
    },
    renderItem(item) {
        return typeof item.get === 'function' ? item.get('text') : item.text;
    },
    extraClass: '',
    containerExtraClass: '',
    groupTitleExtraClass: '',
    usePullRefresh: false,
    onRefresh() {
    },
    useLoadMore: false,
    onLoad() {
    },
    onItemTap() {
    },
    shouldItemUpdate: null,
    itemExtraClass() {
        return '';
    },
    onItemTouchStart() {
    },
    disabled: false,
    directionLockThreshold: 50,
    style: null,
    scrollWithoutTouchStart: true,
    staticSection: null,
    staticSectionHeight: null,
    deceleration: 0.0015,
    stickyOffset: 0
};

const propTypes = {
    /**
     * @property dataSource
     * @type Array/Immutable List
     * @default none
     * @description Immutable ListImmutable Map
     * height()
     * itemHeight(itemHeight)
     * textrenderItemtextlistitem
     */
    dataSource: PropTypes.oneOfType([PropTypes.arrayOf(PropTypes.shape({
        height: PropTypes.number,
        text: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
        key: PropTypes.oneOfType([PropTypes.number, PropTypes.string])
    })), PropTypes.object]).isRequired,
    /**
     * @property renderItem
     * @type Function
     * @default (item)=>item.text
     * @description item()index(index)JSX
     * 
     *
     * :`` renderItem={(item)=><p>{item.someAttr}</p>} ``
     */
    renderItem: PropTypes.func,
    /**
     * @property onItemTap
     * @type Function
     * @default null
     * @param {Object} item 
     * @param {Number} index index
     * @param {DOMElement} target taptarget
     * @description item()index(index)target(event.target)
     *
     * ListiOSListItemListItemonTouchTap
     */
    onItemTap: PropTypes.func,
    /**
     * @property infinite
     * @type Bool
     * @default false
     * @description 
     *
     * infiniteSize
     * ()
     */
    infinite: PropTypes.bool,
    /**
     * @property infiniteSize
     * @type Number
     * @default 12
     * @description ().
     *
     * :
     * (List)
     */
    infiniteSize: PropTypes.number,
    /**
     * @property itemHeight
     * @type Number
     * @default null
     * @description 
     *
     * heightitemHeight
     * offsetHeight
     */
    itemHeight: PropTypes.number,
    /**
     * @property offsetY
     * @type Number
     * @default 0
     * @description Y
     */
    offsetY: PropTypes.number,
    /**
     * @property itemExtraClass
     * @type String/Function
     * @default "item item-wrap"
     * @param {Object} item 
     * @param {Number} index index
     * "custom-list-item"
     * itemdataSourceindexindex
     *
     * :(item)=>{return item.customClassName}/'custom-item-classname'
     * @description class
     */
    itemExtraClass: PropTypes.oneOfType([PropTypes.string, PropTypes.func]),
    /**
     * @skip
     * grouptitle,
     */
    groupTitleExtraClass: PropTypes.string,
    /**
     * @property itemTouchClass
     * @type String/Function
     * @default item-touch
     * @param {Object} item 
     * @param {Number} index index
     * @description classNameitemExtraClass
     */
    itemTouchClass: PropTypes.oneOfType([PropTypes.string, PropTypes.func]),
    /**
     * @property onScroll
     * @type Function
     * @default null
     * @param {Number} y y
     * @param {Enum {'up','down'}} direction ,('up')('down')
     * @description listscrolleronScroll,
     */
    onScroll: PropTypes.func,
    /**
     * @property style
     * @type Object
     * @default null
     * @description List
     * @version 3.0.2
     */
    style: PropTypes.object,
    /**
     * @property onInfiniteAppend
     * @type Function
     * @default null
     * @param {Array} visibleList 
     * @description updatevisibleList()
     */
    onInfiniteAppend: PropTypes.func,
    /**
     * @property staticSection
     * @type Element
     * @default null
     * @version 3.0.3
     * @description Infinite
     */
    staticSection: PropTypes.element,
    /**
     * @property extraClass
     * @type String
     * @default null
     * @description className
     */
    extraClass: PropTypes.string,
    /**
     * @property containerExtraClass
     * @type String
     * @default null
     * @description className(ul.yo-list)
     */
    containerExtraClass: PropTypes.string,
    /**
     * @property usePullRefresh
     * @type Bool
     * @default false
     * @description Scroller
     */
    usePullRefresh: PropTypes.bool,
    /**
     * 
     *
     * @property pullRefreshHeight
     * @type Number
     * @description 
     * @default 40
     */
    pullRefreshHeight: PropTypes.number,
    /**
     * 
     *
     * @property renderPullRefresh
     * @type Function
     * @returns {JSX}  pullRefresh  JSX
     * @description () => JSX
     *
     * 
     */
    renderPullRefresh: PropTypes.func,
    /**
     * @property onRefresh
     * @type Function
     * @param {Array} dataSource 
     * @default null
     * @description 
     */
    onRefresh: PropTypes.func,
    /**
     * @property useLoadMore
     * @type Bool
     * @default false
     * @description Scroller
     */
    useLoadMore: PropTypes.bool,
    /**
     * 
     *
     * @property loadMoreHeight
     * @type Number
     * @description 
     * @default 40
     */
    loadMoreHeight: PropTypes.number,
    /**
     * 
     *
     * @property renderLoadMore
     * @type Function
     * @returns {JSX}  loadMore  JSX
     * @description () => JSX
     *
     * 
     */
    renderLoadMore: PropTypes.func,
    /**
     * @property onLoad
     * @type Function
     * @param {Array} dataSource 
     * @default null
     * @description 
     */
    onLoad: PropTypes.func,
    /**
     * @property shouldItemUpdate
     * @type Function
     * @default null
     * @param {Object} next item
     * @param {Object} now item
     * @description shouldComponentUpdaterender
     *
     * render(Note2)listshouldComponentUpdate
     * item_guid(List)renderrenderrendershouldItemUpdateshouldComponentUpdate
     *
     * shouldItemUpdatenext(ListItempropsitem)
     * now(ListItempropsitem)falserendertruerender(shouldComponentUpdate)
     */
    shouldItemUpdate: PropTypes.func,
    /**
     * @property disabled
     * @type Bool
     * @default false
     * @description Scroller
     */
    disabled: PropTypes.bool,
    /**
     * @property stickyOffset
     * @type Number
     * @default 0
     * @description staticSectiony
     * @version 3.0.6
     */
    stickyOffset: PropTypes.number,
    /**
     * @skip
     * @property onItemTouchStart
     * SwipeMenuList
     */
    onItemTouchStart: PropTypes.func,
    onListItemUpdate: PropTypes.func,
    /**
     * 
     *
     * @property directionLockThreshold
     * @type Number
     * @description 
     * @default 5
     * @version 3.0.2
     */
    directionLockThreshold: PropTypes.number,
    /**
     * @property deceleration
     * @type Number
     * @description 0.001
     * @version 3.0.6
     */
    deceleration: PropTypes.number,
    /**
     * @property scrollWithoutTouchStart
     * @type Bool
     * @default false
     * @description **  **
     * scrollToscrollertouchstartscrollerdisableenable
     * touchstart
     * truescrollertouchmove
     * @version 3.0.2
     */
    scrollWithoutTouchStart: PropTypes.bool
};

export default class List extends Component {

    static INFINITE_SCROLLTO_WITH_ANIMATION_DISTANCE = 2000;

    static childContextTypes = {
        list: PropTypes.object,
        infinite: PropTypes.bool
    };

    constructor(props) {
        super(props);
        const {
            dataSource,
            offsetY,
            itemHeight,
            infinite,
            infiniteSize
        } = props;

        this.childLazyImages = [];
        this.staticSectionContaienr = null;
        this.listModel = new ListModel({
            dataSource,
            offsetY,
            infinite,
            itemHeight,
            infiniteSize
        });
        this.state = {
            visibleList: this.listModel.visibleList,
            totalHeight: this.listModel.totalHeight
        };
    }

    getChildContext() {
        return { list: this, infinite: this.listModel.infinite };
    }

    componentWillMount() {
        this.listModel
            .registerEventHandler('change', (visibleList, totalHeight) => {
                this.setState({ visibleList, totalHeight });
                this.props.onInfiniteAppend(visibleList, totalHeight);
            })
            .registerEventHandler('scrollTo', (offsetY, time, easing) => {
                if (this.scroller) {
                    this.scroller.scrollTo(0, offsetY, time, easing);
                }
            });
    }

    componentDidMount() {
        // ,scroller, didmountdom
        // 
        setTimeout(() => {
            // staticSectionHeight
            this.refreshStaticSectionHeight();
            if (this.scroller) {
                // ,,gesture.js
                this.scroller.isScrolling = false;
            }
            // scroller,infinitetotalHeightitem
            if (this.listModel.infinite && this.scroller) {
                this.scroller.refresh({ scrollerHeight: this.listModel.totalHeight }, true);
            }
            // offsetY,offsetY
            const { offsetY } = this.props;
            if (offsetY !== 0) {
                this.scrollTo(offsetY, 0);
            }
            // lazyload,item
            // setTimeout,
            // didmount,
            this.tryLoadLazyImages(offsetY);
        }, this.listModel.isHeightFixed ? 0 : DELAY_TIME_FOR_INFINITE_WITHOUT_HEIGHT);
    }

    /**
     * @param nextProps
     * dataSource,infiniteSize,reset
     * reset
     */
    componentWillReceiveProps(nextProps) {
        const {
            dataSource,
            infiniteSize,
            offsetY
        } = nextProps;
        this.listModel.refresh({
            dataSource,
            refreshAll: true,
            infiniteSize
        });

        // dom
        setTimeout(() => {
            if (this.props.offsetY !== offsetY) {
                this.scrollTo(offsetY, 0);
            }
            this.refreshStaticSectionHeight();
            this.tryLoadLazyImages(this.listModel.offsetY);
            // offsetY
            if (this.scroller && -this.scroller.maxScrollY < this.listModel.offsetY) {
                this.scrollTo(this.scroller.maxScrollY, 300);
            }
        }, 0);
    }

    componentDidUpdate() {
        // infinitelazyloaddidupdate
        // infinite,render,didupdate,
        // receiveprops,,dom render
        if (!this.listModel.infinite) {
            this.tryLoadLazyImages(this.listModel.offsetY);
        }
        // infinite
        if (this.scroller && this.listModel.infinite) {
            this.scroller.refresh({ scrollerHeight: this.state.totalHeight }, true);
        }
    }

    /**
     * ScollerScrollEnd
     */
    onScrollEnd() {
        // isScrolling
        this.scroller.isScrolling = false;
    }

    /**
     * @param offsetY
     * @param manually
     * ScrollervisibleList
     */
    onScroll(offsetY, manually) {
        if (this.scroller && offsetY !== this.listModel.offsetY) {
            if (!manually) {
                this.scroller.isScrolling = true;
            }
            this.listModel.onScrollTo(offsetY, manually);
            this.props.onScroll(-offsetY, this.listModel.direction);
            this.tryLoadLazyImages(offsetY);
        }
    }

    /**
     * @skip
     * @method refreshStaticSectionHeight
     * @description staticSectionHeight
     */
    refreshStaticSectionHeight() {
        if (this.staticSectionContaienr != null) {
            this.listModel.staticSectionHeight = this.staticSectionContaienr.offsetHeight;
            this.listModel.totalHeight = this.listModel.getTotalHeight();
            // totalHeight
            if (this.scroller && this.listModel.infinite) {
                this.scroller.refresh({ scrollerHeight: this.listModel.totalHeight }, true);
            }
        }
    }

    /**
     * @method refresh
     * @description ,
     */
    refresh() {
        this.scroller.refresh({ scrollerHeight: this.state.totalHeight });
    }

    /**
     * @method stopRefreshing
     * @param {Bool} [successed]  ,false
     * @description (),
     */
    stopRefreshing(successed) {
        if (this.scroller) {
            this.scroller.stopRefreshing(successed);
        }
    }

    /**
     * @method stopLoading
     * @param {Bool} [successed]  ,false
     * @description ,stopRefreshing
     */
    stopLoading(successed) {
        if (this.scroller) {
            this.scroller.stopLoading(successed);
        }
    }

    /**
     * @method startRefreshing
     * @description ,,,
     *
     * :stopRefreshing
     */
    startRefreshing() {
        if (this.scroller.isScrolling) {
            this.scroller.stopAnimate();
            this.scroller.isScrolling = false;
        }

        if (this.listModel.infinite) {
            this.scroller.startRefreshing(0);
            this.onScroll(0, true);
        } else {
            this.scroller.startRefreshing();
        }
    }

    /**
     * lazyimage
     * @param y
     */
    tryLoadLazyImages(y) {
        y = y - this.listModel.staticSectionHeight;
        if (this.childLazyImages.length && this.scroller) {
            this.childLazyImages.forEach((child) => {
                const containerBottomY = y + this.scroller.wrapperHeight;
                if (this.listModel.infinite) {
                    if (containerBottomY > child.itemRef.translateY) {
                        child.load();
                    }
                } else if (child.loading !== 2) {
                    const listItemDom = child.itemRef.domNode;
                    const offsetTop = listItemDom.offsetTop;
                    if (listItemDom && containerBottomY > offsetTop) {
                        child.load();
                    }
                }
            });
        }
    }

    /**
     * @method scrollTo
     * @param {Number} y y
     * @param {Number} [time] ,0
     * (,,time0.)
     * @description List
     */
    scrollTo(offsetY = 0, time = 0) {
        if (this.scroller) {
            // infinite,dom,
            // offetYtime0,2000magic number,
            const aniDuration = this.listModel.infinite ? 0 : time;
            this.scroller.scrollTo(0, offsetY, aniDuration);
            this.onScroll(-offsetY, true);
        }
    }

    /**
     * @method stopAnimate
     * @description 
     */
    stopAnimate() {
        if (this.scroller) {
            this.scroller.stopAnimate();
        }
    }

    /**
     * @param item
     * @param i
     * @returns {JSX}
     * 
     */
    renderItemWrap(item, i) {
        const {
            onItemTap,
            renderItem,
            onListItemUpdate,
            groupTitleExtraClass,
            shouldItemUpdate,
            onItemTouchStart
        } = this.props;
        const { itemTouchClass, itemExtraClass } = this.props;
        let realActiveClass = itemTouchClass,
            realExtraClass = itemExtraClass;
        // itemExtraClassstring,
        // stringstringid
        if (typeof itemExtraClass === 'string' || itemExtraClass === null) {
            realExtraClass = () => itemExtraClass;
        }
        // the same
        if (typeof itemTouchClass === 'string' || itemTouchClass === null) {
            realActiveClass = () => itemTouchClass;
        }

        return (
            <ListItem
                parent={this}
                itemTouchClass={realActiveClass}
                key={this.listModel.infinite ? i : item.key}
                renderItem={renderItem}
                onItemTap={(target) => {
                    onItemTap(item.srcData, item._index, target);
                }}
                shouldItemUpdate={shouldItemUpdate}
                onItemTouchStart={onItemTouchStart}
                item={item}
                itemExtraClass={realExtraClass}
                groupTitleExtraClass={groupTitleExtraClass}
                listModel={this.listModel}
                onListItemUpdate={onListItemUpdate}
            />
        );
    }

    render() {
        const {
            containerExtraClass,
            infiniteSize,
            onRefresh,
            onLoad
        } = this.props;
        const { infinite } = this.listModel;
        const containerClass = classNames(
            'yo-list',
            containerExtraClass,
            infinite ? 'yo-list-infinite' : ''
        );
        const { visibleList } = this.state;

        return (
            <Scroller
                {...inheritProps(this.props, [
                    'scrollWithoutTouchStart',
                    'style',
                    'directionLockThreshold',
                    'disabled',
                    'extraClass',
                    'pullRefreshHeight',
                    'renderPullRefresh',
                    'loadMoreHeight',
                    'renderLoadMore',
                    'useLoadMore',
                    'usePullRefresh',
                    'deceleration',
                    'stickyOffset'
                ])}
                tap={true}
                autoRefresh={!infinite}
                ref={(scroller) => {
                    if (scroller) {
                        this.scroller = scroller;
                    }
                }}
                onScroll={(evt) => this.onScroll(-evt.contentOffset.y)}
                onScrollEnd={() => this.onScrollEnd()}
                onRefresh={() => {
                    onRefresh(this.listModel.dataSource);
                }}
                onLoad={() => {
                    onLoad(this.listModel.dataSource);
                }}
                enableLazyLoad={false}
            >
                {this.props.staticSection != null ?
                    <div
                        ref={dom => {
                            if (dom) {
                                this.staticSectionContaienr = dom;
                            }
                        }}
                        className="yo-list-static-section"
                    >
                        {this.props.staticSection}
                    </div> : null}
                <ul
                    className={containerClass}
                    ref={(dom) => {
                        this.listContainer = dom;
                    }}
                >
                    {infinite ?
                        // ,,translateY
                        getArrayByLength(infiniteSize).fill(1).map((__, i) => {
                            const item = visibleList.find((it) => it._order === i);
                            return item ? this.renderItemWrap(item, i) : null;
                        }) :
                        // ,item
                        visibleList.map((item, i) =>
                            this.renderItemWrap(item, i)
                        )}
                </ul>
            </Scroller>
        );
    }
}

List.defaultProps = defaultProps;
List.propTypes = propTypes;
List.LazyImage = LazyImage;



