import './style.scss';
import React, { Component, PropTypes } from 'react';
import aniScrollX from './aniScrollx.js';
import PureRenderMixin from 'react-addons-pure-render-mixin';
import CarouselItem from './carouselItem';

const Dots = props => {
    let liNodes = [];
    for (let i = 0; i < props.num; i++) {
        liNodes.push(<li key={i} className={props.page === i + 1 ? 'on' : ''} />);
    }
    return (
        <ul className="index">
            {liNodes}
        </ul>
    );
};
Dots.propTypes = {
    num: PropTypes.number,
    page: PropTypes.number
};

const DEFAULTANI = aniScrollX();

class Carousel extends Component {
    static propTypes = {
        /**
         * @property dots
         * @type Bool
         * @default true
         * @description demo
         */
        dots: PropTypes.bool,
        /**
         * @property autoplay
         * @type Bool
         * @default true
         * @description 
         */
        autoplay: PropTypes.bool,
        /**
         * @property loop
         * @type Bool
         * @default true
         * @description  
         */
        loop: PropTypes.bool,
        /**
         * @property beforeChange
         * @type Function
         * @param {num} 
         * @description carousel.children1
         */
        beforeChange: PropTypes.func,
        /**
         * @property afterChange
         * @type Function
         * @param {num} 
         * @description carousel.children1
         */
        afterChange: PropTypes.func,
        /**
         * @property extraClass
         * @type String
         * @description class
         */
        extraClass: PropTypes.string,
        /**
         * @property delay
         * @type Number
         * @description s
         */
        delay: PropTypes.number,
        /**
         * @property speed
         * @type Number
         * @description s,
         */
        speed: PropTypes.number,
        /**
         * @property defaultPage
         * @type Number
         * @description 
         */
        defaultPage: PropTypes.number,
        /**
         * @property aniSpeed
         * @type Number
         * @description css
         */
        aniSpeed: PropTypes.number,
        /**
         * @property aniObj
         * @type Object
         * @description 
         *
         * - handleDataaniObj, children
         * - touchstart(aniObj) touchstart
         * - touchmove(aniObj) touchmove
         * - touchend(aniObj) touchend
         * - touchcancel(aniObj)touchcancel
         * - next(aniObj)  
         * - arriveaniObj,num) 
         * - prev(aniObj)  
         *
         * carousel
         * + aniCssIndex
         * + aniInfinate3listinfintedomdomdom
         *
         * **aniObj**
         *
         * ```
         * {
         *    aniSpeed:0,
         *    containerDOM: ul.cont, //
         *    delay: 1,
         *    loop: true,
         *    operationTimer: 5, //
         *    pageNow: 5,
         *    speed: .5,
         *    stageDOM: div,
         *    width: 375 //mount
         *    touchstartLocation:e
         *    touchendLocation:e
         *    touchmoveLocation:e
         * }
         * ```
         */
        aniObj: PropTypes.object,
        /**
         * @property children
         * @type Element
         * @description carousel
         */
        children: PropTypes.array.isRequired
    }
    static defaultProps = {
        dots: true,
        autoplay: true,
        loop: true,
        effect: 'scrollX',
        delay: 1.5,
        speed: 0.5,
        defaultPage: 1,
        aniSpeed: 0,
        beforeChange() {
        },
        afterChange() {
        }
    }
    static childContextTypes = {
        /**
         * @property currentPage
         * @type PropTypes.number
         * @description contextcurrentPagecurrentPagepage
         */
        currentPage: PropTypes.number,
        /**
         * @property pagesNum
         * @type PropTypes.number
         * @description contextpagesNumpagesNumcarouselchildren
         */
        pagesNum: PropTypes.number
    }

    constructor(props) {
        super(props);
        this.state = {
            page: 1
        };
        this.shouldComponentUpdate = PureRenderMixin.shouldComponentUpdate.bind(this);
        this.dragDom = null;
        this.dragEvt = null;
    }

    getChildContext() {
        return {
            currentPage: this.state.page,
            pagesNum: this.props.children.length
        };
    }

    componentWillMount() {
        this.ani = Object.assign({}, this.props.aniObj || DEFAULTANI);
        this.aniObj = {
            delay: this.props.delay,
            speed: this.props.speed,
            pageNow: 1,
            pagesNum: this.props.children.length,
            aniSpeed: this.props.aniSpeed,
            loop: this.props.loop,
            operationTimer: 0,
            touchstartLocation: {},
            touchendLocation: {}
        };
    }

    componentDidMount() {
        this.aniObj.stageDOM = this.widgetDOM.parentNode;
        this.aniObj.width = this.widgetDOM.clientWidth;
        this.aniObj.containerDOM = this.widgetDOM.querySelector('.cont');
        this.arrive(this.props.defaultPage, false);
        this.launchAuto();
    }

    componentWillReceiveProps(props) {
        this.aniObj.delay = props.delay;
        this.aniObj.speed = props.speed;
        this.aniObj.pagesNum = props.children.length;
        this.aniObj.aniSpeed = props.aniSpeed;
        this.aniObj.loop = props.loop;
    }

    componentWillUpdate(nextProps, nextState) {
        if (nextState.page !== this.state.page) {
            this.props.beforeChange(nextState.page);
        }
    }

    componentDidUpdate(prevProps, prevState) {
        // this.aniObj.stageWidth = this.widgetDOM.clientWidth;
        if (prevState.page !== this.state.page) {
            this.props.afterChange(this.state.page);
        }
        if (prevProps.autoplay !== this.props.autoplay || prevProps.loop !== this.props.loop) {
            this.pause();
            this.play();
        }
    }

    componentWillUnmount() {
        this.pause();
    }

    /**
     * @description 
     * @method arrive
     * @param  {number} num 
     * @param {isAni} boolean 
     */
    arrive(num, isAni = true) {
        this.aniObj.operationTimer = num - 1;
        this.pause();
        if (num > 0 && num <= React.Children.count(this.props.children)) {
            const page = this.ani.arrive(this.aniObj, num, isAni);
            this.setState({
                page
            });
            this.aniObj.pageNow = page;
        }
        this.play();
    }

    launchAuto() {
        if (this.autoplay) {
            window.clearInterval(this.autoplay);
        }
        if (this.props.autoplay &&
            (this.props.loop || this.aniObj.pageNow < this.aniObj.pagesNum)
        ) {
            this.autoplay = window.setInterval(() => {
                this.next();
            }, this.props.delay * 1000);
        }
    }

    format(children) {
        const childrenList = React.Children.map(children, (childElement, index) => (
            React.cloneElement(childElement, {
                index: index + 1
            })
        ));
        return this.ani.handleData(this.aniObj, childrenList);
    }

    // getEndX(distanceX) {
    //     let pageNow = this.aniObj.pageNow;
    //     if (Math.abs(distanceX) < 40) {
    //         return -(pageNow - 1);
    //     }
    //     if (distanceX > 0) {
    //         pageNow = pageNow - 2;
    //         this.aniObj.operationTimer --;
    //     } else {
    //         this.aniObj.operationTimer ++;
    //     }
    //     return -pageNow;
    // }
    /**
     * @method play
     * @description 
     */
    play() {
        this.launchAuto();
    }

    /**
     * @method pause
     * @description 
     */
    pause() {
        if (this.autoplay) {
            window.clearInterval(this.autoplay);
        }
    }

    /**
     * @method prev
     * @description 
     */
    prev() {
        this.aniObj.operationTimer--;
        const page = this.ani.prev(this.aniObj);
        this.setState({ page });
        this.aniObj.pageNow = page;
    }

    /**
     * @method next
     * @description 
     */
    next() {
        this.aniObj.operationTimer++;
        const page = this.ani.next(this.aniObj);
        this.setState({ page });
        this.aniObj.pageNow = page;
        if (page >= this.aniObj.pagesNum && !this.props.loop) {
            this.pause();
        }
    }

    handleTouchStart(e) {
        e.preventDefault();
        // e.stopPropagation();
        this.pause();
        this.aniObj.touchstartList = e.touches[0];
        this.aniObj.touchstartLocation = [e.touches[0].clientX, e.touches[0].clientY];
        this.ani.touchstart(this.aniObj);
    }

    handleTouchMove(e) {
        e.preventDefault();
        // e.stopPropagation();
        this.aniObj.touchmoveList = e.touches[0];
        this.aniObj.touchmoveLocation = [e.touches[0].clientX, e.touches[0].clientY];

        this.ani.touchmove(this.aniObj);
    }

    handleTouchEnd(e) {
        e.preventDefault();
        // e.stopPropagation();
        this.aniObj.touchendList = e.touches.length > 0 ?
            e.touches[0]
            : this.aniObj.touchmoveList;
        if (!this.aniObj.touchendList) {
            return;
        }
        this.aniObj.touchendLocation = [
            this.aniObj.touchendList.clientX,
            this.aniObj.touchendList.clientY
        ];
        this.aniObj.pageNow = this.ani.touchend(this.aniObj);
        this.setState({
            page: this.aniObj.pageNow
        });
        this.play();
        this.clearTouchList();
    }

    handleTouchCancle(e) {
        e.preventDefault();
        // e.stopPropagation();
        if (this.ani.touchcancel) {
            this.ani.touchcancel(this.aniObj);
            return;
        }
        this.aniObj.touchendList = this.aniObj.touchmoveList;
        this.aniObj.touchendLocation = [
            this.aniObj.touchendList.clientX,
            this.aniObj.touchendList.clientY
        ];
        this.aniObj.pageNow = this.ani.touchend(this.aniObj);
        this.setState({
            page: this.aniObj.pageNow
        });
        this.clearTouchList();
    }

    clearTouchList() {
        Object.assign(this.aniObj, {
            touchstartList: [],
            touchmoveList: [],
            touchstartLocation: [],
            touchmoveLocation: [],
            touchendLocation: []
        });
    }

    render() {
        const classList = ['yo-carousel'];
        if (this.props.extraClass != null) {
            classList.push(this.props.extraClass);
        }
        let children = this.format(this.props.children);
        return (
            <div
                className={classList.join(' ')}
                ref={(node) => {
                    if (node) {
                        this.widgetDOM = node;
                    }
                }}
                onTouchStart={evt => {
                    this.handleTouchStart(evt);
                }}
                onTouchMove={evt => {
                    this.handleTouchMove(evt);
                }}
                onTouchEnd={evt => {
                    this.handleTouchEnd(evt);
                }}
                onTouchCancel={evt => {
                    // this.dragEvt.dragCancel(evt)
                    this.handleTouchCancle(evt);
                }}
            >
                <ul className={'cont'}>
                    {children}
                </ul>
                {this.props.dots ? <Dots num={this.aniObj.pagesNum} page={this.state.page} /> : ''}
            </div>
        );
    }

}
Carousel.CarouselItem = CarouselItem;
Carousel.Item = CarouselItem;

export default Carousel;



